.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.center{
  align-self: center;
  align-content: center;
}
/* Form styling */
form {
  background: #f9f9f9;
  border:1px solid #c9e6ff;
  margin: 1em auto 0 auto;
  padding: 1em;
}
form select,
form input {
  border:1px solid #c9e6ff;
}

form button {
  border: none;
  padding: 1em;
  background: #0069ed;
  color: #ffffff;
  cursor: pointer;
}

form button:hover,
form button:focus {
  background: #0053ba;
}

form button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

form button:active {
  transform: scale(0.99);
}

.geosuggest__suggests--hidden {
  max-height: 0;
  overflow: hidden;
  border-width: 0;
}
.geosuggest__item--active {
  background: #267dc0;
  color: #fff;
}

select,
input {
  padding: 0.7em;
  margin-bottom: 0.5rem;
}

select:focus,
input:focus {
  outline: 2px solid royalblue;
}

@media (min-width: 700px) {
  form {
    grid-template-columns: auto 1fr;
    grid-gap: 16px;
  }

  button {
    grid-column: 2 / 3;
    justify-self: center;
  }

  nav div {
    flex-direction: row;
  }
  nav li:last-child {
    border-bottom: 1px white solid;
  }

  nav ul {
    display: flex;
    flex: 1;
    /*justify-content: flex-end;*/
    align-items: center;
  }

  main {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }
}


h3 {
  text-align: center;
}

/* table styling */
table {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #ddd;
  padding: 8px;
}

tr:nth-child(even){background-color: #f2f2f2;}
tr:hover {background-color: #ddd;}
th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #0069ed;
  color: white;
}

.float{
  position:fixed;
  width:60px;
  height:60px;
  right: 40px;
  margin: 10px;
  background-color:#0C9;
  color:#FFF;
  border-radius:50px;
  text-align:center;
  box-shadow: 2px 2px 3px #999;
}

.my-float{
  margin-top:22px;
}
